import { Component, Prop, h } from '@stencil/core';

export type LoaderSize = 'sm' | 'md' | 'lg' | 'xl';
export type LoaderVariant = 'default' | 'white';

@Component({
  tag: 'bh-loader-spinner',
  styleUrl: 'bh-loader-spinner.css',
  shadow: true,
})
export class BhLoaderSpinner {
  /** Loader size */
  @Prop() size: LoaderSize = 'md';

  /** Loader color variant - use 'white' for dark surfaces like buttons */
  @Prop() variant: LoaderVariant = 'default';

  /** Whether to show as overlay */
  @Prop() overlay: boolean = false;

  /** Message to show in overlay mode */
  @Prop() overlayMessage: string = '';

  /** Whether to show message */
  @Prop() showMessage: boolean = false;

  /** Message text */
  @Prop() message: string = 'Loading...';

  private renderSpinner() {
    const gradientId = this.variant === 'white' ? 'bellhop-gradient-white' : 'bellhop-gradient';

    return (
      <div class={`loader-spinner variant-${this.variant}`}>
        <div class="spinner-gradient">
          <svg class="gradient-svg" viewBox="0 0 50 50">
            <defs>
              {this.variant === 'white' ? (
                <linearGradient
                  id="bellhop-gradient-white"
                  x1="0%"
                  y1="0%"
                  x2="0%"
                  y2="100%"
                >
                  <stop offset="0%" style={{ stopColor: 'rgba(255, 255, 255, 1)' }} />
                  <stop offset="50%" style={{ stopColor: 'rgba(255, 255, 255, 0.8)' }} />
                  <stop offset="100%" style={{ stopColor: 'rgba(255, 255, 255, 0.5)' }} />
                </linearGradient>
              ) : (
                <linearGradient
                  id="bellhop-gradient"
                  x1="0%"
                  y1="0%"
                  x2="0%"
                  y2="100%"
                >
                  <stop offset="0%" style={{ stopColor: 'var(--color-accent-300)' }} />
                  <stop offset="51%" style={{ stopColor: 'var(--color-brand-400)' }} />
                  <stop offset="100%" style={{ stopColor: 'var(--color-brand-600)' }} />
                </linearGradient>
              )}
            </defs>
            <circle
              class="gradient-path"
              cx="25"
              cy="25"
              r="20"
              fill="none"
              stroke={`url(#${gradientId})`}
              stroke-width="4"
              stroke-linecap="round"
            />
          </svg>
        </div>
      </div>
    );
  }

  private renderMessage(isOverlay: boolean) {
    const shouldShowMessage = isOverlay
      ? (this.showMessage || this.overlayMessage)
      : this.showMessage;

    if (!shouldShowMessage) return null;

    return (
      <div class="loader-message">
        {isOverlay ? (this.overlayMessage || this.message) : this.message}
      </div>
    );
  }

  render() {
    if (this.overlay) {
      return (
        <div class="loader-overlay">
          <div class="loader-overlay-content">
            <div class={`loader-container size-${this.size}`}>
              {this.renderSpinner()}
            </div>
            {this.renderMessage(true)}
          </div>
        </div>
      );
    }

    return (
      <div class={`loader-container size-${this.size}`}>
        {this.renderSpinner()}
        {this.renderMessage(false)}
      </div>
    );
  }
}
